import { Reducer } from 'redux';
import { FriendsState } from './types';

const initialState: FriendsState = {
  loaded: false,
  loading: false,
  friends: [],
  userNotFound: false,
  newFriend: undefined
};

const reducer: Reducer<FriendsState> = (state = initialState, action) => {
  switch (action.type) {
    case 'LOAD_FRIENDS': {
      return {
        ...state,
        loading: true,
      };
    }
    case 'SET_FRIENDS': {
      return {
        ...state,
        friends: action.newFriendList,
      };
    }
    case 'FRIENDS_LOADED': {
      return {
        ...state,
        friends: action && action.data && action.data.filter((el:any) => el),
        loading: false,
        loaded: true,
      };
    }
    case 'FRIEND_NOT_FOUND': {
      return {
        ...state,
        userNotFound: true
      };
    }
    case 'FRIEND_FOUND_AND_ADDED': {
      const { newFriend } = action;
      return {
        ...state,
        userNotFound: false,
        newFriend
      };
    }
    
    default:
      return { ...state };
  }
};

export default reducer;